@import "reset";
@import "vars";
@import "mixins";

@font-face {
  font-family: "Big Noodle Titling";
  src: url("../fonts/bignoodletitling/big_noodle_titling.ttf") format("opentype");
  font-weight: normal;
  font-style: normal; }

.navbar {
  background-color: #f8f6f6; }

.brand {
  font-family: "Big Noodle Titling";
  @include font(40px, 400, 4px);
  color: #696969;
  height: 145px;
  transition: color 0.5s; }

.brand:hover {
  text-decoration: none;
  color: $button-color; }

.navbar-toggler {
  border: none; }

.navbar-toggler:hover {
  outline: 1px solid #c0c0c0; }

.navbar-toggler:focus {
  outline: 1px solid #c0c0c0; }

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
  width: 38px; }

.navbar-nav {
  width: 100%;
  justify-content: flex-end; }

.nav-link {
  color: #696969;
  transition: color 0.5s; }

.nav-link:hover {
  color: $button-color; }

.jumbotron {
  //background-color: #3d3d3d
  background: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url("../img/bg.jpg");
  background-size: cover; }

.jumbotron__h1 {
  margin-top: 320px;
  font-family: "Big Noodle Titling";
  @include font(72px, 400, 10.8px);
  color: #d6d5d5; }

.jumbotron__p {
  font-family: "Big Noodle Titling";
  @include font(36px, 400, 3.6px);
  color: #d6d5d5;
  margin-bottom: 170px; }

.pe-7s-mouse {
  color: #d6d5d5;
  margin-bottom: 15px; }

.jumbotron__p-bot {
  font-family: Roboto;
  font-size: 12px;
  font-weight: 700;
  color: #d6d5d5;
  margin-bottom: 45px; }

.about-us__h1 {
  margin-top: 100px;
  color: #3d3d3d;
  font-family: "Big Noodle Titling";
  @include font(48px, 400, 4.8px);
  margin-bottom: 10px; }

.about-us__hr {
  width: 153px;
  height: 3px;
  background-color: $hr-color;
  margin-top: 0;
  margin-bottom: 25px; }

.about-us__p {
  width: 520px;
  color: #696969;
  font-family: "Roboto - Thin";
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  margin-top: 0;
  margin-bottom: 55px; }

.about-us__col {
  background-color: #ebebeb;
  margin-bottom: 120px;
  i {
    color: $hr-color;
    margin-top: 30px; } }

.about-us__h2 {
  color: #707070;
  font-family: Roboto;
  @include font(18px, 700, 0.45px);
  margin-top: 25px; }

.about-us__p2 {
  width: 244px;
  color: #707070;
  font-family: "Roboto - Thin";
  @include font(14px, 400, 0.35px);
  text-align: center;
  margin-top: 15px;
  margin-bottom: 25px; }

.our-works {
  background-color: #f8f6f6; }

.our-works__h1 {
  margin-top: 75px;
  color: #3d3d3d;
  font-family: "Big Noodle Titling";
  @include font(48px, 400, 4.8px);
  margin-bottom: 10px; }

.our-works__hr {
  width: 153px;
  height: 3px;
  background-color: $hr-color;
  margin-top: 0;
  margin-bottom: 25px; }

.our-works__p {
  width: 520px;
  color: #696969;
  font-family: "Roboto - Thin";
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  margin-top: 0;
  margin-bottom: 85px; }

.our-works__filters {
  width: 750px; }

.our-works__filter {
  display: block;
  color: #3d3d3d;
  font-family: Roboto;
  @include font(14px, 400, 1.4px);
  text-decoration: none;
  padding-top: 7px;
  padding-bottom: 7px;
  margin-bottom: 25px;
  text-align: center;
  transition: color 0.5s, font-weight 0.5s; }

.our-works__filter__txt {
  border-bottom: 2px solid #f8f6f6; }

.our-works__filter:hover {
  color: #3d3d3d;
  font-weight: 700;
  text-decoration: none;
  .our-works__filter__txt {
    border-bottom: 2px solid $button-color; } }

.our-works__filter-active {
  color: #3d3d3d;
  font-weight: 700;
  .our-works__filter__txt {
    border-bottom: 2px solid $button-color; } }

.our-works__col {
  height: 175px; }

.our-works__img {
  width: 100%;
  height: 100%;
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: $button-color;
    opacity: 0;
    transition: 0.5s linear; }
  img {
    width: 100%;
    height: 100%; } }

.our-works__col:hover {
  .our-works__img:after {
    opacity: 85%; } }

.clients {
  height: 240px;
  background-color: #1a1a1a; }

.clients__arrow {
  color: #5d5d5d;
  transition: color 0.5s; }

.clients__content {
  a:hover .clients__arrow {
    color: $button-color; } }

.who-we-are__h1 {
  margin-top: 80px;
  color: #3d3d3d;
  font-family: "Big Noodle Titling";
  @include font(48px, 400, 4.8px);
  margin-bottom: 10px; }

.who-we-are__hr {
  width: 153px;
  height: 3px;
  background-color: $hr-color;
  margin-top: 0;
  margin-bottom: 25px; }

.who-we-are__p {
  width: 520px;
  color: #696969;
  font-family: "Roboto - Thin";
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  margin-top: 0;
  margin-bottom: 85px; }

.who-we-are__content {
  margin-bottom: 105px; }

.who-we-are__member {
  background-color: #ebebeb;
  margin-right: 25px;
  img {
    width: 134px;
    height: 134px;
    border-radius: 67px;
    margin-top: 70px;
    margin-bottom: 31px; }
  h1 {
    color: #3d3d3d;
    font-family: Roboto;
    @include font(20px, 700, 2px);
    justify-content: center;
    margin-bottom: 6px; }
  p {
    color: #3d3d3d;
    font-family: Roboto;
    @include font(14px, 300, 1.4px);
    margin-bottom: 22px; }
  img:last-of-type {
    width: 50px;
    height: 11px;
    margin-top: 0;
    margin-bottom: 80px; } }

.who-we-are__icons {
  margin-bottom: 40px;
  a {
    text-decoration: none;
    color: #696969;
    transition: color 0.5s; }
  a:hover {
    color: #c0c0c0; } }

.who-we-are__skills {
  background-color: #ebebeb; }

.who-we-are__container {
  margin-top: 125px;
  margin-left: 65px;
  margin-right: 50px;
  h1 {
    color: #3d3d3d;
    font-family: "Roboto - Bold Condensed";
    @include font(24px, 600, 2.4px);
    margin-bottom: 26px; }
  p {
    color: #858383;
    font-family: "Roboto - Thin";
    @include font(14px, 400, 0.5px);
    margin-bottom: 42px; } }

.skill {
  h1 {
    color: #3d3d3d;
    font-family: Roboto;
    @include font(14px, 700, 1.4px);
    margin-bottom: 0; }
  p {
    color: #3d3d3d;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 0; } }


.skill__progress {
  padding: 0; }

.progress__left {
  width: 88%;
  height: 1px;
  background-color: $button-color; }

.progress__right {
  width: 12%;
  height: 1px;
  background-color: #fff; }

.progress__left-1 {
  width: 91%; }

.progress__right-1 {
  width: 9%; }

.progress__left-2 {
  width: 90%; }

.progress__right-2 {
  width: 10%; }

.progress__left-3 {
  width: 98%; }

.progress__right-3 {
  width: 2%; }

.features {
  background-color: #171f23;
  h1 {
    margin-top: 60px;
    margin-bottom: 20px;
    color: #f6f6f6;
    font-family: "Roboto - Condensed";
    @include font(36px, 400, 7.2px); }
  hr {
    width: 86px;
    height: 1px;
    background-color: #404040;
    margin-bottom: 45px;
    margin-top: 0; } }

.features__row {
  margin-bottom: 70px; }

.features__icon {
  color: $icon-color; }

.features__h1 {
  color: #f6f6f6;
  font-family: Roboto;
  font-size: 18px;
  font-weight: 300;
  margin-top: 32px;
  margin-bottom: 31px; }

.features__p {
  color: #f6f6f6;
  font-family: Roboto;
  font-size: 48px;
  font-weight: 300; }

.blog-post__h1 {
  margin-top: 80px;
  color: #3d3d3d;
  font-family: "Big Noodle Titling";
  @include font(48px, 400, 4.8px);
  margin-bottom: 10px; }

.blog-post__hr {
  width: 153px;
  height: 3px;
  background-color: $hr-color;
  margin-top: 0;
  margin-bottom: 25px; }

.blog-post__p {
  width: 520px;
  color: #696969;
  font-family: "Roboto - Thin";
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  margin-top: 0;
  margin-bottom: 72px; }

.card-deck {
  margin-bottom: 115px;
  a {
    img {
      transition: 0.5s filter linear; } }
  a:hover {
    text-decoration: none;
    img {
      filter: brightness(30%); } } }

.card__body {
  background-color: #ebebeb;
  padding: 30px;
  h5 {
    padding-top: 10px;
    padding-bottom: 30px;
    color: #505050;
    font-family: Roboto;
    @include font(18px, 300, 1.8px);
    margin-bottom: 0; }
  p {
    color: #797979;
    font-family: "Roboto - Thin";
    @include font(14px, 400, 0.7px); } }

.card-footer {
  border: none;
  background-color: #ebebeb;
  padding-bottom: 26px;
  padding-left: 30px;
  padding-right: 30px; }

.text-muted {
  i {
    margin-right: 7px; } }

.plan {
  background-color: #171f23;
  h1 {
    margin-top: 60px;
    margin-bottom: 20px;
    color: #f6f6f6;
    font-family: "Roboto - Condensed";
    @include font(36px, 400, 7.2px); }
  hr {
    width: 86px;
    height: 1px;
    background-color: #404040;
    margin-bottom: 56px;
    margin-top: 0; } }

.plan__block {
  border: 1px solid #171f23;
  transition: border 0.5s; }

.plan__block:hover {
  border: 1px solid $button-color;
  .plan__footer p span {
    color: $button-color; } }

.plan__row {
  margin-bottom: 70px; }

.plan__header {
  background-color: #0e1316;
  padding-top: 40px;
  padding-bottom: 40px; }

.plan__h6 {
  color: #ffffff;
  font-family: Roboto;
  @include font(14px, 700, 1.41px);
  margin-bottom: 9px;
  text-align: center; }

.plan__price {
  color: #ffffff;
  font-family: Roboto;
  font-size: 36px;
  font-weight: 300;
  text-align: center; }

.plan__footer {
  background-color: #1f282c;
  padding-top: 40px;
  p {
    color: #ffffff;
    font-weight: 100;
    letter-spacing: 1.41px;
    text-align: center;
    margin-bottom: 20px; }
  p:last-of-type {
    padding-bottom: 30px;
    margin-bottom: 0; }
  span {
    font-family: Roboto;
    font-weight: 500;
    letter-spacing: 1.41px;
    transition: color 0.5s; }
  a {
    display: inline-block;
    padding: 11px 22px;
    border-radius: 3px;
    background-color: $button-color;
    color: #ffffff;
    font-family: Roboto;
    @include font(14px, 700, 1.4px);
    margin: 45px auto;
    outline: 1px solid $button-color;
    transition: color 0.5s, background-color 0.5s; }
  a:hover {
    background-color: #1f282c;
    color: $button-color;
    text-decoration: none; } }

.touch__h1 {
  margin-top: 80px;
  color: #3d3d3d;
  font-family: "Big Noodle Titling";
  @include font(48px, 400, 4.8px);
  margin-bottom: 10px; }

.touch__hr {
  width: 153px;
  height: 3px;
  background-color: $hr-color;
  margin-top: 0;
  margin-bottom: 25px; }

.touch__p {
  width: 520px;
  color: #696969;
  font-family: "Roboto - Thin";
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  margin-top: 0;
  margin-bottom: 93px; }

.touch__form__p {
  color: #696969;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 700;
  span {
    color: $button-color; } }

.touch__input {
  border-bottom: 1px solid #cacaca;
  width: 100%;
  padding: 5px; }

.touch__textarea {
  border-bottom: 1px solid #cacaca;
  width: 100%;
  padding: 5px;
  height: 80px;
  resize: none; }

.touch__form {
  a {
    display: inline-block;
    padding: 11px 22px;
    border-radius: 3px;
    background-color: $button-color;
    color: #ffffff;
    font-family: Roboto;
    @include font(14px, 700, 1.4px);
    margin: 100px auto;
    outline: 1px solid $button-color;
    transition: color 0.5s, background-color 0.5s; }
  a:hover {
    background-color: #fff;
    color: $button-color;
    text-decoration: none; } }

.footer {
  background-color: #171f23; }

.footer__content {
  padding: 50px 0;
  a {
    color: #ffffff;
    font-family: "Big Noodle Titling";
    @include font(24px, 400, 2.4px);
    transition: color 0.5s; }
  a:hover {
    text-decoration: none;
    color: $button-color; }
  p {
    color: #ffffff;
    font-family: "Roboto - Condensed";
    @include font(14px, 400, 1.4px);
    text-align: center;
    margin-bottom: 0; } }

.footer__icons {
  margin-bottom: 40px;
  text-align: center;
  a {
    text-decoration: none;
    color: #e2e0e0;
    transition: color 0.5s;
    i {
      font-size: 14px; } }
  a:hover {
    color: $icon-color; } }
